/* -------------------------------------------------------------- 
# Fluid container
# 
# Utilizes the mixin meant for fixed width containers, but with 
# 100% width for fluid, full width layouts.
-------------------------------------------------------------- */
.container-fluid {
  width: 100%;
  padding-right: 15px;
  padding-left: 15px;
  margin-right: auto;
  margin-left: auto;
}

/* -------------------------------------------------------------- 
# Row
# 
# Rows contain and clear the floats of your columns.
-------------------------------------------------------------- */
.row {
  display: flex;
  flex-wrap: wrap;
  margin-right: -15px;
  margin-left: -15px;
}

/* -------------------------------------------------------------- 
	# Remove the negative margin from default .row, then the 
	# horizontal padding from all immediate children columns 
	# (to prevent runaway style inheritance).
	-------------------------------------------------------------- */
.no-gutters {
  margin-right: 0;
  margin-left: 0;
}

.no-gutters > .col,
.no-gutters > [class*="col-"] {
  padding-right: 0;
  padding-left: 0;
}

