============================================================== */
/* Text meant only for screen readers. */
.srt-only {
  clip: rect(1px, 1px, 1px, 1px);
  position: absolute !important;
  height: 1px;
  width: 1px;
  overflow: hidden;
  word-wrap: normal !important;
  /* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.srt-only:focus {
  background-color: #f1f1f1;
  border-radius: 3px;
  box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
  clip: auto !important;
  color: #21759b;
  display: block;
  font-size: 14px;
  font-size: 0.875rem;
  font-weight: bold;
  height: auto;
  left: 5px;
  line-height: normal;
  padding: 15px 23px 14px;
  text-decoration: none;
  top: 5px;
  width: auto;
  z-index: 100000;
}

/* Do not show the outline on the skip link target. */
[tabindex="-1"]:focus {
  outline: 0;
}

/* ==============================================================
# Alignment
============================================================== */
.align-left {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.align-right {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.align-center {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/*--------------------------------------------------------------
# Text alignment helpers
 --------------------------------------------------------------*/
.text-align-left {
  text-align: left !important;
}

.text-align-center {
  text-align: center !important;
}

.text-align-right {
  text-align: right !important;
}

/*--------------------------------------------------------------
 * Vertically center content
 --------------------------------------------------------------*/
.bull-v1-center-wrapper {
  display: table;
  height: 100%;
  width: 100%;
}

.bull-v1-center-wrapper.bull-v1-center-wrapper--full-screen {
  height: 100vh;
  width: 100vw;
}

.bull-v1-center-wrapper .bull-v1-center-wrapper__content {
  display: table-cell;
  height: 100%;
  width: 100%;
  vertical-align: middle;
}

/* ==============================================================
# Posisitioning
============================================================== */
/* Clear inner floats*/
.cf:before,
.cf:after {
  content: "";
  display: table;
}

.cf:after {
  clear: both;
}

.cf {
  *zoom: 1;
}
